.widget_nav_menu > div > ul,
.widget_pages > ul {
  border-radius: 0px;
  box-shadow: 0 0 0 1px #7b7b7b;
  background: #ffffff;
}

.widget_nav_menu > div > ul .active > a,
.widget_nav_menu > div > ul .current-menu-item > a,
.widget_pages > ul .active > a,
.widget_pages > ul .current-menu-item > a {
  font-weight: 700;
  text-decoration: underline;
}

.widget_nav_menu > div > ul a,
.widget_pages > ul a {
  font-size: 18px;
  font-weight: 400;
  color: #ff4d4d;
  padding: 12px 20px;
  padding-left: 36px;
  display: block;
  position: relative;
  text-decoration: none;
  z-index: 1;
}

.widget_nav_menu > div > ul a:hover,
.widget_pages > ul a:hover {
  text-decoration: underline;
}

.widget_nav_menu > div > ul a::after,
.widget_pages > ul a::after {
  content: "";
  display: block;
  width: 6px;
  height: 6px;
  position: absolute;
  left: 17px;
  top: 22px;
  background: #ff4d4d;
  border-radius: 0;
}

.widget_nav_menu > div > ul a::before,
.widget_pages > ul a::before {
  content: "";
  display: block;
  width: calc(100% - 20px);
  height: 1px;
  background: #7b7b7b;
  position: absolute;
  bottom: -1px;
  left: 10px;
}

.widget_categories:not(.widget_categories_select) ul {
  border-radius: 0px;
  box-shadow: 0 0 0 1px #7b7b7b;
  background: #ffffff;
}

.widget_categories:not(.widget_categories_select) ul li {
  font-size: 18px;
  font-weight: 400;
  color: #ff4d4d;
  padding: 12px 20px;
  padding-left: 36px;
  display: block;
  position: relative;
  text-decoration: none;
  z-index: 1;
  display: flex;
}

.widget_categories:not(.widget_categories_select) ul li::after {
  content: "";
  display: block;
  width: 6px;
  height: 6px;
  position: absolute;
  left: 17px;
  top: 22px;
  background: #ff4d4d;
  border-radius: 0;
}

.widget_categories:not(.widget_categories_select) ul li::before {
  content: "";
  display: block;
  width: calc(100% - 20px);
  height: 1px;
  background: #7b7b7b;
  position: absolute;
  bottom: -1px;
  left: 10px;
}

.widget_categories:not(.widget_categories_select) ul li a {
  width: 100%;
}

.widget_categories:not(.widget_categories_select) ul li a:hover {
