  /*
	.aside-nav {
		box-shadow: 0 0 0 1px $aside-nav-border-color;
		border-radius: $aside-nav-border-radius;
	}
	*/
  .aside-nav nav {
    display: none;
    box-shadow: none;
  }
  .widget {
    display: none;
  }
  .widget_nav_menu,
  .widget_pages,
  .widget_categories {
    display: block;
    box-shadow: 0 0 0 1px #7b7b7b;
    border-radius: 0px;
  }
  .widget_nav_menu .widgettitle,
  .widget_pages .widgettitle,
  .widget_categories .widgettitle {
    font-size: 18px;
    position: relative;
    line-height: 24px;
    padding: 12px 10px;
    padding-right: 40px;
    margin-bottom: 0;
    transform: none;
  }
  .widget_nav_menu .widgettitle::before,
  .widget_pages .widgettitle::before,
  .widget_categories .widgettitle::before {
    content: "";
    display: block;
    width: 25px;
    height: 25px;
    position: absolute;
    right: 10px;
    top: 50%;
    top: 12px;
    background-image: url("images/ico/caret-down.svg");
    background-repeat: no-repeat;
    background-position: center center;
    background-size: contain;
    transition: transform 0.3s;
  }
  .widget_nav_menu .widgettitle.js-open::before,
  .widget_pages .widgettitle.js-open::before,
  .widget_categories .widgettitle.js-open::before {
    transform: rotate(180deg);
  }
  .widget_categories ul,
  .widget_nav_menu > div > ul,
  .widget_pages > ul {
    display: none;
