.loading::before {
  content: "";
  background-color: transparent;
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 20;
  display: block;
  height: 50px;
  width: 50px;
  border-radius: 50%;
  animation: rotate 2s infinite linear;
  margin-top: -15px;
  margin-left: -15px;
  background-image: url("images/ico/spinner.svg");
}
